<template>
  <!-- 试听预约记录 -->
  <div>
    <Table
      v-if="tableData.length"
      border
      :columns="columns"
      :data="tableData"
      :maxHeight="300"
    >
    </Table>
    <p v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);margin-top: 10px;">暂无数据</p>
    <!-- 班级详情 -->
    <tal-class-detail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
      isDisplayExportBtn
    >
    </tal-class-detail>
  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import classDetailMixin from '@/mixins/classDetail'
import { auditionAppointmentList } from '@/api/audition'
export default {
  name: 'auditionReservationRecord',
  mixins: [CustomerMixin, classDetailMixin],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      tableData: [],
      // 考勤状态
      attendenceStatus: {
        0: '缺勤',
        1: '准时上课',
        2: '迟到'
      },
      columns: Object.freeze([
        {
          title: '预约顾问',
          key: 'planAdvisorName',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.planAdvisorName || '无')
          }
        },
        {
          title: '接待顾问',
          minWidth: 82,
          key: 'treatAdvisorName',
          align: 'left',
          render: (h, params) => {
            return h('span', params.row.treatAdvisorName || '无')
          }
        },
        {
          title: '用户意向',
          key: 'stuIntention',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.stuIntention || '无')
          }
        },
        {
          title: '渠道标签',
          key: 'channel',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.channel || '无')
          }
        },
        {
          title: '创建时间',
          key: 'createTime',
          align: 'left',
          minWidth: 105,
          render: (h, params) => {
            return h('span', params.row.createTime || '无')
          }
        },
        {
          title: '是否取消',
          key: 'appointmentStatus',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.appointmentStatus === 5 ? '是' : '否')
          }
        },
        {
          title: '考勤状态',
          key: 'isAttendance',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            const attendenceName =
              this.attendenceStatus[params.row.isAttendance] || '--'
            return h('span', attendenceName)
          }
        },
        {
          title: '年份',
          key: 'year',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.year || '--')
          }
        },
        {
          title: '试听学期',
          key: 'term',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.term || '--')
          }
        },
        {
          title: '试听学科',
          key: 'subject',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.subject || '--')
          }
        },
        {
          title: '试听年级',
          key: 'grade',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.grade || '--')
          }
        },
        {
          title: '试听课程',
          key: 'className',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return params.row.className
              ? h(
                'a',
                {
                  on: {
                    click: () => {
                      this.showClassDetail(params.row.classId)
                    }
                  }
                },
                params.row.className
              )
              : h('span', '--')
          }
        },
        {
          title: '试听课次',
          key: 'curriculumNum',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.curriculumNum || '--')
          }
        },
        {
          title: '上课时间',
          key: 'classTimeVal',
          align: 'left',
          minWidth: 105,
          render: (h, params) => {
            return h('span', params.row.classTimeVal || '--')
          }
        },
        {
          title: '试听服务中心',
          key: 'serviceCenter',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.serviceCenter || '--')
          }
        },
        {
          title: '是否报名同学科长期班',
          key: 'isRegist',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.isRegist === 1 ? '是' : '否')
          }
        },
        {
          title: '是否缴费同学科长期班',
          key: 'isPay',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.isPay === 1 ? '是' : '否')
          }
        },
        {
          title: '跟进次数',
          key: 'followCount',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.followCount)
          }
        }
      ])
    }
  },
  methods: {
    // 散诊记录查询
    initView () {
      const params = {
        stuCode: this.formData.card,
        uid: this.formData.uid,
        cityCode: this.formData.cityCode,
        pageSize: 10,
        pageNo: 1
      }
      auditionAppointmentList(params)
        .then((res) => {
          this.tableData = Object.freeze(res.auditionAppointmentResps) || []
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
        .finally(() => {
          this.tableLoading = false
        })
    }
  }
}
</script>

