<!--顾问维护记录详情-->
<template>
  <div>
    <Modal
      :mask-closable="false"
      title="顾问维护记录详情"
      v-model="modalValue"
      :footer-hide="true"
      width="641px">
      <div class="maintain-record-detail">
        <Row class="mb30">
          <Col span="24">
            <span class="txt-right">任务名称：</span>
            <span class="txt-left">{{data.otherInfo.taskNameList.join('、') || '其他'}}</span>
          </Col>
        </Row>
        <Row class="mb30">
          <Col span="12">
              <span class="txt-right">维护时间：</span>
              <span class="txt-left">{{data.createTime}}</span>
          </Col>
          <Col span="12">
            <span class="txt-right">沟通方式：</span>
            <span class="txt-left">{{data.followTypeName}}</span>
          </Col>
        </Row>
        <Row class="mb30">
          <Col span="12">
              <span class="txt-right">达成成果：</span>
              <span class="txt-left">{{data.followResultName}}</span>
          </Col>
          <Col span="12">
            <span class="txt-right">维护人：</span>
            <span class="txt-left">{{data.userName}}（{{data.otherInfo.serviceCenter}}）</span>
          </Col>
        </Row>
        <Row class="mb30" v-if="data.showAppointment">
          <Col span="12" >
            <span class="txt-right">预约沟通时间：</span>
            <span class="txt-left">{{data.appointmentTime}}</span>
          </Col>
          <Col span="12">
            <span class="txt-right">预约内容：</span>
            <span class="txt-left">{{data.appointmentContent}}</span>
          </Col>
        </Row>
        <Row class="mb10">
          <span class="txt-right">沟通内容：</span>
        </Row>
        <Row class="mb30">
          <Col span="24">
            <Input :disabled="true" :value="data.content" type="textarea" :autosize="{minRows: 2,maxRows: 5}"></Input>
          </Col>
        </Row>
        <template v-if="data.followFlag">
          <Row class="mb10">
            <span class="txt-right">其他结果：</span>
          </Row>
          <Row class="mb10">
            <Col span="24">
              <Input :disabled="true" :value="data.followFlag" type="textarea" :autosize="{minRows: 2,maxRows: 5}"></Input>
            </Col>
          </Row>
        </template>
        <template v-if="data.otherInfo && data.otherInfo.wxChatRecordList && data.otherInfo.wxChatRecordList.length">
          <Row class="mb10">
            <span class="txt-right">天策沟通内容：</span>
          </Row>
          <Row class="mb10">
            <div v-for="item in data.otherInfo.wxChatRecordList" :key="item.assistantWxName" class="ivu-table ivu-table-default ivu-table-stripe ivu-table-wrapper">
              <div class="ivu-table-body">
                <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
                  <thead>
                  <tr class="ivu-table-row">
                    <th class="ivu-table-column-center" style="width: 100px"><div class="ivu-table-cell">发送方</div></th>
                    <th class="ivu-table-column-center" style="width: 150px"><div class="ivu-table-cell">时间</div></th>
                    <th class="ivu-table-column-center"><div class="ivu-table-cell">沟通内容</div></th>
                  </tr>
                  </thead>
                  <tbody class="ivu-table-tbody">
                    <template v-if="item.msgVOList && item.msgVOList.length > 0">
                      <tr class="ivu-table-row" v-for="(msg, i) in item.msgVOList" :key="i">
                        <td class="ivu-table-column-center"><div class="ivu-table-cell">{{msg.sendWxName === item.assistantWxName ?  `顾问（${data.userName}）` :  '学员'}}</div></td>
                        <td class="ivu-table-column-center"><div class="ivu-table-cell">{{msg.formattedSendTime}}</div></td>
                        <td>
                          <div class="ivu-table-cell">
                            <!--图片-->
                            <template v-if="msg.contentType === 2">
                              图片:<a @click="viewMsg(msg)">点击查看</a>
                            </template>
                            <!--语音-->
                            <template v-else-if="msg.contentType === 3">
                              语音:<a @click="viewMsg(msg)">点击播放</a>
                            </template>
                            <!--视频-->
                            <template v-else-if="msg.contentType === 4">
                              视频:<a @click="viewMsg(msg)">点击查看</a>
                            </template>
                            <!--名片-->
                            <template v-else-if="msg.contentType === 9">
                              【名片：{{ getCardName(msg) }}】
                            </template>
                            <template v-else>
                              {{msg.msg}}
                            </template>
                          </div>
                        </td>
                      </tr>
                    </template>
                    <template v-else>
                      <tr>
                        <td class="ivu-table-column-center" colspan="3"><div class="ivu-table-cell">暂无数据</div></td>
                      </tr>
                    </template>
                  </tbody>
                </table>
              </div>
            </div>
          </Row>
        </template>
      </div>
      <Modal :mask-closable="false"
             title="消息查看"
             v-model="msgShow"
             :footer-hide="true"
             width="700px">
        <img v-if="msgType === 2" :src="msgUrl" width="668"/>
        <video v-if="msgType === 4" controls="controls" autoplay :src="msgUrl" width="668">
          您的浏览器不支持 video 标签。
        </video>
      </Modal>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'maintainRecordDetail',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      default: () => {
        return {

        }
      }
    }
  },
  data () {
    return {
      msgShow: false,
      msgType: null,
      msgUrl: null
    }
  },
  methods: {
    viewMsg (item) {
      const obj = JSON.parse(item.msg)
      this.msgType = item.contentType
      if (item.contentType === 3) {
        const mp3 = new Audio(obj.url)
        mp3.play()
      } else {
        this.msgUrl = obj.url
        this.msgShow = true
      }
    },
    getCardName (item) {
      const obj = JSON.parse(item.msg)
      return obj.nickName
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.show
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  }
}
</script>

